// Portfolio Ligthbox
const lightbox = document.querySelector('.lightbox');
const lightboxImg = lightbox.querySelector('.lightbox-img');
const lightboxClose = lightbox.querySelector('.lightbox-close');
const lightboxText = lightbox.querySelector('.caption-text');
const lightboxCounter = lightbox.querySelector('.caption-counter');

let itemIndex = 0;

function toggleLightbox() {
  lightbox.classList.toggle('open');
}

function changeItem() {
  const imgSrc = portfolioItems[itemIndex].querySelector('.portfolio-img img').getAttribute('src');
  lightboxImg.src = imgSrc;

  lightboxText.innerHTML = portfolioItems[itemIndex].querySelector('h4').innerHTML;
  lightboxCounter.innerHTML = `${itemIndex + 1} of ${totalPortfolioItem}`;
}

for (let i = 0; i < totalPortfolioItem; i++) {
  portfolioItems[i].addEventListener('click', function() {
    itemIndex = i;
    changeItem();
    toggleLightbox();
  });
}

// close Lightbox

lightbox.addEventListener('click', function(event) {
  if (event.target === lightboxClose || event.target === lightbox) {
    toggleLightbox();
  }
});

const test1 = document.getElementById('prev');
test1.addEventListener('click', () => {
  if (itemIndex === totalPortfolioItem - 1) {
    itemIndex = 0;
  } else {
    itemIndex += 1;
  }

  changeItem();
});

const test2 = document.getElementById('next');
test2.addEventListener('click', () => {
  if (itemIndex === 0) {
    itemIndex = totalPortfolioItem - 1;
  } else {
    itemIndex -= 1;
  }
  changeItem();
});

const test3 = document.getElementById('nextPic');
test3.addEventListener('click', () => {
  if (itemIndex === totalPortfolioItem - 1) {
    itemIndex = 0;
  } else {
    itemIndex += 1;
  }

  changeItem();
});
